<template>
    <div class="manger-view">
        <h3>SWE TimeTable</h3>
<!-- <export-excel
    class   = "btn btn-default"
    :data   = "json_data"
    :fields = "json_fields"
    worksheet = "My Worksheet"
    name    = "filename.xls">
 
    Download Excel 
 
